.Welcome {
    padding-top: 20vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.Welcome--input,
.Welcome--input:focus {
    font-family: 'Poppins', sans-serif;
    width: 500px;
    text-align: center;
    margin-top: 50px;
    margin-bottom: 50px;
    background: rgba(255, 255, 255, 0);
    border-width: 0 0 1px 0; /* top right bottom left */
    border-style: solid;
    border-color: rgba(128, 128, 128, 0.336);
    padding-bottom: 16px;
    font-size: 18px;
    font-style: italic;
    font-weight: 200;
    outline: 0;
}

.Welcome--button {
    font-family: 'Poppins', sans-serif;
    width: 120px;
    height: 40px;
    border-radius: 30px;
    border: 1px solid rgba(128, 128, 128, 0.2);
    background: rgba(255, 255, 255, 0);
    font-weight: 600;
    color: #3b0078;
    -webkit-box-shadow: 0 0 10px rgba(128, 128, 128, 0.2);
    box-shadow: 0 0 10px rgba(128, 128, 128, 0.2);
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
}

.arrow {
    display: none;
}

.Welcome--button:hover .arrow {
    display: block;
    width: 12px;
    padding-left: 12px;
    animation: slow-down-arrow 0.3s
}

@keyframes slow-down-arrow {
    0% {
         opacity: 0;
         transform: scale(0)
    }

    50% {
        opacity: 0.5;
        transform: scale(0.5)
    }

    100% {
         opacity: 1;
         transform: scale(1)
    }
}

