@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,300;0,400;0,600;1,200&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  height: 100%;
  background: url(/zoovu/static/media/desktop-1.f44e5475.svg) no-repeat center center fixed;
  background-size: cover;
  font-family: 'Poppins', sans-serif;
}

.App {
  text-align: center;
}


.Welcome {
    padding-top: 20vh;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

.Welcome--input,
.Welcome--input:focus {
    font-family: 'Poppins', sans-serif;
    width: 500px;
    text-align: center;
    margin-top: 50px;
    margin-bottom: 50px;
    background: rgba(255, 255, 255, 0);
    border-width: 0 0 1px 0; /* top right bottom left */
    border-style: solid;
    border-color: rgba(128, 128, 128, 0.336);
    padding-bottom: 16px;
    font-size: 18px;
    font-style: italic;
    font-weight: 200;
    outline: 0;
}

.Welcome--button {
    font-family: 'Poppins', sans-serif;
    width: 120px;
    height: 40px;
    border-radius: 30px;
    border: 1px solid rgba(128, 128, 128, 0.2);
    background: rgba(255, 255, 255, 0);
    font-weight: 600;
    color: #3b0078;
    box-shadow: 0 0 10px rgba(128, 128, 128, 0.2);
    cursor: pointer;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.arrow {
    display: none;
}

.Welcome--button:hover .arrow {
    display: block;
    width: 12px;
    padding-left: 12px;
    -webkit-animation: slow-down-arrow 0.3s;
            animation: slow-down-arrow 0.3s
}

@-webkit-keyframes slow-down-arrow {
    0% {
         opacity: 0;
         -webkit-transform: scale(0);
                 transform: scale(0)
    }

    50% {
        opacity: 0.5;
        -webkit-transform: scale(0.5);
                transform: scale(0.5)
    }

    100% {
         opacity: 1;
         -webkit-transform: scale(1);
                 transform: scale(1)
    }
}

@keyframes slow-down-arrow {
    0% {
         opacity: 0;
         -webkit-transform: scale(0);
                 transform: scale(0)
    }

    50% {
        opacity: 0.5;
        -webkit-transform: scale(0.5);
                transform: scale(0.5)
    }

    100% {
         opacity: 1;
         -webkit-transform: scale(1);
                 transform: scale(1)
    }
}


.GameView {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    padding: 10px 40px;
}

.GameView--name_and_scores {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.GameView--name_and_scores h1 {
    color: lightseagreen;
}

.GameView--name_and_scores h3 {
    line-height: 2.2;
}

.GameView--name_and_scores h3:nth-child(1) {
    text-align: left;
}

.GameView--name_and_scores h3:nth-child(2) {
    text-align: right;
    color: #3b0078;
}

.GameView--name_and_scores img{
    width: 16px;
}

.GameView span {
    color: rgba(18, 8, 61, 0.479);
    font-size: 12px;
    letter-spacing: 1px;
}

.GameView--cards {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin: 40px 0;
}

.GameView--span {
    text-align: left;
    padding-top: 25px;
}

.GameView--cards_containers{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin: 25px 0;
}

.GameView--card_container {
    width: 225px;
    height: 225px;
    background-image: url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='aliceblue' rx='15' ry='15' stroke='lightseagreen' stroke-width='4' stroke-dasharray='6%2c 14' stroke-dashoffset='23' stroke-linecap='square'/%3e%3c/svg%3e");
    border-radius: 15px;
}

.GameView--Droppable {
    width: 225px;
    height: 225px;
}

@media only screen and (min-width: 1441px) {
    .GameView--card_container,
    .GameView--Droppable {
        width: 260px;
        height: 260px;
    }
}

@media only screen and (min-width: 1024px) and (max-width: 1279px) {
    .GameView--card_container,
    .GameView--Droppable {
        width: 155px;
        height: 155px;
    }
}

@media only screen and (min-width: 620px) and (max-width: 999px) {
    .GameView--card_container,
    .GameView--Droppable {
        width: 105px;
        height: 105px;
    }
}

@media only screen and (max-width: 619px) {
    .GameView--card_container,
    .GameView--Droppable {
        width: 50px;
        height: 50px;
    }
}
